// Menu para enseÃ±ar y ocultar
const navMenu = document.getElementById('nav-menu'),
  navToggle = document.getElementById('nav-toggle'),
  navClose = document.getElementById('nav-close');

//Mostrar
if (navToggle) {
  navToggle.addEventListener('click', () => {
    navMenu.classList.add('mostrar-menu');
  });
}

//Ocultar
if (navClose) {
  navClose.addEventListener('click', () => {
    navMenu.classList.remove('mostrar-menu');
  });
}

//Remover menu movil
const navLink = document.querySelectorAll('.nav__link');

function linkAction() {
  const navMenu = document.getElementById('nav-menu');
  navMenu.classList.remove('mostrar-menu');
}

navLink.forEach((n) => n.addEventListener('click', linkAction));

//Mostrar Caracteristicas de las habilidades
const habilidadesContent = document.getElementsByClassName(
    '.habilidades__contenido'
  ),
  habilidadesHeader = document.querySelectorAll('.habilidades__header');

function habilitarSkills() {
  let itemClass = this.parentNode.className;

  for (i = 0; i < habilidadesContent.length; i++) {
    habilidadesContent[i].className =
      'habilidades__contenido habilidad__cerrar';
  }
  if (itemClass === 'habilidades__contenido habilidad__cerrar') {
    this.parentNode.className = 'habilidades__contenido habilidad__abrir';
  } else {
    this.parentNode.className = 'habilidades__contenido habilidad__cerrar';
  }
}

habilidadesHeader.forEach((e) => e.addEventListener('click', habilitarSkills));

// Mostrar los servicios al seleccionar ver mas
const modalViews = document.querySelectorAll('.servicios__modal'),
  modalbtns = document.querySelectorAll('.servicios__button'),
  modalCerrar = document.querySelectorAll('.servicios__modal-cerrar');

let modal = function (modalClick) {
  modalViews[modalClick].classList.add('active-modal');
};

modalbtns.forEach((modalbtns, i) => {
  modalbtns.addEventListener('click', () => {
    modal(i);
  });
});

modalCerrar.forEach((modalCerrar) => {
  modalCerrar.addEventListener('click', () => {
    modalViews.forEach((modalViews) => {
      modalViews.classList.remove('active-modal');
    });
  });
});

// Slider para el portafolio
let swiper = new Swiper('.portafolio__contenedor', {
  cssMode: true,
  loop: true,
  navigation: {
    nextEl: '.swiper-button-next',
    prevEl: '.swiper-button-prev',
  },
  pagination: {
    el: '.swiper-pagination',
    clickable: true,
  },
});

// SCROLL SECCIONES
const sections = document.querySelectorAll('section[id]');

function scrollActive() {
  const scrollY = window.pageYOffset;

  sections.forEach((current) => {
    const sectionHeight = current.offsetHeight;
    const sectionTop = current.offsetTop - 50;
    sectionId = current.getAttribute('id');

    if (scrollY > sectionTop && scrollY <= sectionTop + sectionHeight) {
      document
        .querySelector('.nav__menu a[href*=' + sectionId + ']')
        .classList.add('active-link');
    } else {
      document
        .querySelector('.nav__menu a[href*=' + sectionId + ']')
        .classList.remove('active-link');
    }
  });
}
window.addEventListener('scroll', scrollActive);

// Cambiar color del header
function scrollHeader() {
  const nav = document.getElementById('header');
  // When the scroll is greater than 200 viewport height, add the scroll-header class to the header tag
  if (this.scrollY >= 200) nav.classList.add('scroll-header');
  else nav.classList.remove('scroll-header');
}
window.addEventListener('scroll', scrollHeader);

// mostrar scroll hacia arriba
function scrollUp() {
  const scrollUp = document.getElementById('scroll-up');
  // When the scroll is higher than 560 viewport height, add the show-scroll class to the a tag with the scroll-top class
  if (this.scrollY >= 560) scrollUp.classList.add('show-scroll');
  else scrollUp.classList.remove('show-scroll');
}
window.addEventListener('scroll', scrollUp);

/*==================== MODO OSCURO ====================*/
const themeButton = document.getElementById('theme-button');
const darkTheme = 'dark-theme';
const iconTheme = 'uil-sun';

// Previously selected topic (if user selected)
const selectedTheme = localStorage.getItem('selected-theme');
const selectedIcon = localStorage.getItem('selected-icon');

// We obtain the current theme that the interface has by validating the dark-theme class
const getCurrentTheme = () =>
  document.body.classList.contains(darkTheme) ? 'dark' : 'light';
const getCurrentIcon = () =>
  themeButton.classList.contains(iconTheme) ? 'uil-moon' : 'uil-sun';

// We validate if the user previously chose a topic
if (selectedTheme) {
  // If the validation is fulfilled, we ask what the issue was to know if we activated or deactivated the dark
  document.body.classList[selectedTheme === 'dark' ? 'add' : 'remove'](
    darkTheme
  );
  themeButton.classList[selectedIcon === 'uil-moon' ? 'add' : 'remove'](
    iconTheme
  );
}

// Activate / deactivate the theme manually with the button
themeButton.addEventListener('click', () => {
  // Add or remove the dark / icon theme
  document.body.classList.toggle(darkTheme);
  themeButton.classList.toggle(iconTheme);
  // We save the theme and the current icon that the user chose
  localStorage.setItem('selected-theme', getCurrentTheme());
  localStorage.setItem('selected-icon', getCurrentIcon());
});
